export const algorithm = {
    name: "Breadth First Search",
    run: function (cells, cellStart, cellFinish, pathStatus) {
        let node = { x: cellStart.x, y: cellStart.y, parent: null };

        const queue = [node];
        while (queue.length) {
            let current = queue.shift();
            if ((current.x !== cellStart.x) || (current.y !== cellStart.y))
                cells[current.y][current.x] = pathStatus;
            if ((current.x === cellFinish.x) && (current.y === cellFinish.y)) {
                // const path = backtrace(current);
                // TODO: something
                return;
            }
            console.log(current);
            const next = neighbours(current, 0, 0, cells[0].length - 1, cells.length - 1);
            for (let i = 0; i < next.length; ++i)
                if ((cells[next[i].y][next[i].x] !== pathStatus) && !((next[i].x === cellStart.x) && (next[i].y === cellStart.y)) && (queue.findIndex(n => (n.x === next[i].x) && (n.y === next[i].y)) === -1))
                    queue.push(next[i]);
        }
    }
};

function backtrace(node) {
    const path = [{x: node.x, y: node.y}];
    while (node.parent !== null) {
        node = node.parent;
        path.push({x: node.x, y: node.y});
    }
    return path;
}

function neighbours(node, minX, minY, maxX, maxY) {
    const res = [];
    if (node.x < maxX)
        res.push({ x: node.x + 1, y: node.y, parent: node });
    if (node.x > minX)
        res.push({ x: node.x - 1, y: node.y, parent: node });
    if (node.y < maxY)
        res.push({ x: node.x, y: node.y + 1, parent: node });
    if (node.y > minY)
        res.push({ x: node.x, y: node.y - 1, parent: node });
    return res;
}